<template>
  <div class="fault_info">
    <div class="itemTit">
      <span class="border-yellow"></span>
    </div>
    <!-- -->
    <div class="fault_list">
      <item v-for="item of annoucements" :key="item.id" v-bind:item="item" />
    </div>
  </div>
</template>

<script>
import item from "./item.vue";
import virtualList from "vue-virtual-scroll-list"; //
export default {
  components: { "virtual-list": virtualList, item },
  name: "zdinfo",
  data() {
    return {
      annoucements: [], //badge//;variant:warning/danger/info;title json
      info: {
        badge: "danger",
        tid: "",
        content: "",
        ctime: "",
        lo: "",
        la: "",
        type: "",
      }, // tid:id content
    };
  },
  methods: {
    get: function () {
      //get
      this.$http.get("api/device/mlal").then(
        function (res) {
          for (let i = 0; i < res.data.data.length; i++) {
            this.info = {
              id: i,
              badge: "danger",
              tid: res.data.data[i].tid,
              content: res.data.data[i].content,
              ctime: res.data.data[i].ctime,
              lo: res.data.data[i].lo,
              la: res.data.data[i].la,
              type: "",
            };
            this.annoucements.push(this.info); //json
          }
          this.$store.commit("SET_ZDINFO", this.annoucements);
        },
        function () {
          console.log("");
        }
      );
    },
  },
  mounted: function () {
    //
    this.get();
    //setInterval(this.get,6000)//get
  },
};
</script>
<style >
.itemTit {
  padding: 12px 30px;
  border-bottom: 1px solid #1e5b6b;
}
.itemTit span {
  display: block;
  height: 30px;
  line-height: 30px;
  border-left: 5px solid transparent;
  font-size: 15px;
  color: #fff;
  padding-left: 8px;
  text-align: left;
}
.border-yellow {
  border-color: #ffd220 !important;
}
.fault_list {
  overflow: scroll;
  overflow-x: hidden;
  height: 53.5vh;
}
</style>



